import React, { useEffect, useRef, useCallback, useState} from 'react';
import {useAudioPlayer} from 'components/hooks/useAudioPlayer.js';
import ipfs from 'decentralized/Ipfs.js';
import AudioUnit from 'engine/audio.js';
import {REMIX_COLLECTION_ADDRESS} from 'constants/zpore-addresses.js';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import {connect} from 'react-redux';
import ZorbRemixView from './ZorbRemixView.js';
import './remixes.scss';

const ZorbRemixes = (props) => { 
    let [remixes, setRemixes] = useState([]);
    let [show, setShow] = useState(false);
    let [hash, setHash] = useState();
    let {audioPlayer, loaded, setLoaded} = useAudioPlayer(hash, true, true);
    let [playing, setPlaying] = useState();

    useEffect(() => {
        if (playing) {
            let recordingHash = playing.sporeHash;
            setHash(recordingHash);
        }
    }, [playing, audioPlayer, remixes, setHash]);

    useEffect(() => {
        getSporeRemixes(REMIX_COLLECTION_ADDRESS, 1)
            .then(result => {
                setRemixes(result);
            });
    }, []);

    if (show) {
        return <ZorbRemixView
                 audioPlayer={audioPlayer}
                 playing={playing}
                 setPlaying={setPlaying}
                 hide={() => {
                     if (audioPlayer) {
                         audioPlayer.stopSound();
                     }
                     setShow(false);
                 }} remixes={remixes}/>;
    }
    return (
        <div
          onClick={() => setShow(true)}
          className="remixes-button">
          <span>{remixes.length} Remixes </span>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbRemixes);
