import React, { useRef, useEffect, useCallback, useState} from 'react';
import ENSName from 'components/ens/ENSName.js';
import ipfs from 'decentralized/Ipfs.js';
import {getZorb} from 'utils/getZorb.js';
import {connect} from 'react-redux';
import "./zorb-remix-view.scss";
import SporeRoots from './SporeRoots.js';

const ZorbRemixView = ({remixes, hide, audioPlayer, setPlaying, playing}) => { 
    let [refs, setRefs] = useState([]);
    let rootRef = useRef();

    return (
        <div className="zorb-remix-view">
          <SporeRoots rootRef={rootRef} refs={refs}/>
          <div onClick={hide} className="back-btn">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-left" viewBox="0 0 16 16"> <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"/> </svg>
          </div>
          <div ref={rootRef} className="ancestral-source">
            <div className="song-name">
              Psilocybin
            </div>
            <div className="artist-name">
              Keyon Christ
            </div>
          </div>
          <div className="zorb-children">
            {remixes.map(
                x => <ZorbRemix
                       audioPlayer={audioPlayer}
                       refs={refs}
                                setPlaying={setPlaying} playing={playing} setRefs={setRefs} remix={x}/>)}
          </div>
        </div>);
};

const ZorbRemix = ({refs, setRefs, remix, playing, audioPlayer, setPlaying}) => {
    const ref = useRef();
    const [zorb, setZorb] = useState();
    const [data, setData] = useState();

    useEffect(() => {
        ipfs.getJson(remix.dataHash).then(x => {
            if (x.zorbId) {
                getZorb(x.zorbId).then(setZorb);
                setData(x);
            }
        });
    }, [remix]);
    useEffect(() => {
        if (zorb && !refs.includes(ref)) {
            setRefs([... refs, ref]);
        }
    }, [zorb, refs, setRefs]);

    if (!zorb) {
        return '';
    }

    return (<div
              onClick={() => setPlaying(remix)}
              ref={ref} className={playing === remix ? "zorb-remix-option playing" : "zorb-remix-option"}>
              <div className="inner">
              {zorb && <img className="zorb-image" src={zorb}/>}
              <div className="caption">
                {data.caption}
              </div>
              </div>
              <ENSName address={remix.creator}/>
              {playing && <div
                            onClick={(e) => {
                                e.stopPropagation();
                                audioPlayer.stopSound()
                                setPlaying(undefined);
                            }}

                            className="pause-button"/>}
            </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbRemixView);
