import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const StepRuler = ({width, buffer, startStep, bpm=90}) => { 
    let duration = buffer[0].length/44100;
    // figure out how many beats to divide by assuming 90 beats per minute

    // how many minutes
    let durationMin = duration / 60;
    let beatCount = Math.floor(durationMin * bpm);

    // offset by startSteps % to 32

    let l = [];
    for (let i=0; i < beatCount; i++) {
        l.push(i);
    }

    startStep += 6;
    let mod = startStep % 32;
    // how much of the bar is it off by
    let ratio = mod / 32;
    let offset = ratio*width/l.length;

    return (
        <div style={{left: offset+'px'}} className="step-ruler">
          {l.map(x => <div style={{ width: width / l.length}} className="beat"/>)}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StepRuler);
