import React, {useRef, useState, useCallback, useEffect} from 'react';
import {getRuler} from './getRuler.js';
import {SECONDS_PER_STEP} from './utils.js';

export const useWaveformSlicer = ({ref, bpm=90, width, buffer, startStep, silence, waveform}) => {
    let [start, setStart] = useState(0);
    let [length, setLength] = useState(100);
    let [draggingStart, setDraggingStart] = useState();
    let [draggingEnd, setDraggingEnd] = useState();
    let lengthRef = useRef(length);

    let {beats}  = getRuler({buffer, start, silence, pts: waveform, width, bpm, startStep});

    useEffect(() => {
        lengthRef.current = length;
    }, [length]);

    const quantizeLength = useCallback((percentage) => {
        let duration = buffer[0].length/44100;
        let time = percentage/100*(duration);
        let secondsPerStep = SECONDS_PER_STEP(bpm);

        // secsPerStep/steps = time
        let steps = time / secondsPerStep ;
        let modded = (16 - (steps % 16));

        if (modded > 8) {
            modded = -(steps % 16);
        }

        // see the mod from the beat

        let moddedTime = secondsPerStep * modded;

        let moddedPercentage = 100 * moddedTime / duration;

        return percentage + moddedPercentage;
    }, [buffer]);

    const quantize = useCallback((percentage) => {
        let _beats = beats.map(x => x/(2*width)*100);
        // find the percentage that is closest

        let minDistance = 100000;
        let min = -1;
        for (let beat of _beats) {
            let dist = Math.abs(beat-percentage);
            if (dist < minDistance) {
                min = beat;
                minDistance = dist;
            }
        }
        return min;
        /*
        let duration = buffer[0].length/44100;
        let startStepTime = (startStep/8)*bpm/60;
        console.log('start step time=%s and silence=%s', startStepTime, silence);
        if (startStepTime > 0) {
            silence*= 0.8;
        }
        let time = startStepTime - silence + percentage/100*(duration); // in seconds
        console.log("REALIZED SECONDS=", time);


        let secondsPerStep = SECONDS_PER_STEP(bpm);

        // secsPerStep/steps = time


        let steps = time / secondsPerStep ;

        // convert this back to steps
        
        let modded = (8 - (steps % 8) );

        if (modded > 4) {
            modded = -(steps % 8);
        }


        // see the mod from the beat

        let moddedTime = secondsPerStep * modded;

        let moddedPercentage = 100 * moddedTime / duration;

        // conver this mod to time

        // convert time to percentage

        // add this to the initial percentage
        let x = percentage + moddedPercentage;
        if (startStep !== 0) {
            x += 100*silence / duration
        }


        let modOffsetSeconds = (32 - (startStep % 32) + 8)*secondsPerStep;

        return x;
        */
    }, [startStep, buffer, silence, bpm, beats]);
        
    const onMouseMove = useCallback((e) => {
        if (draggingStart === undefined && draggingEnd === undefined) {
            return;
        }
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();

        let x = e.pageX - rect.left;
        if (draggingStart !== undefined) {
            let diff = 100*(x - draggingStart.x) / (width*2);
            let newStart = draggingStart.start + diff;
            newStart = Math.max(0, Math.min(100, newStart));
            setStart(newStart);
            setLength(Math.min(100 - newStart, draggingStart.length - diff));
        } else {
            let diff = 100*(x - draggingEnd.x) / (width*2);
            let newLength = draggingEnd.length + diff;
            setLength(Math.min(100 - start, Math.max(0, newLength)));
        }

    }, [draggingStart, width, draggingEnd, start, length, setLength, setStart, [startStep, buffer, silence, bpm]]);

    const dragStartControl = useCallback((e) => {
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        setDraggingStart({x, start, length});
    }, [setDraggingStart, start, length]);

    const dragEndControl = useCallback((e) => {
        if (e.touches) {
            e = e.touches[0];
        }
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        setDraggingEnd({x, length});
    }, [setDraggingEnd, length]);

    const onMouseUp = useCallback(() => {
        if (draggingStart || draggingEnd) {
            let quantized = quantize(start);
            setStart(quantized);
            let quantizedLength = quantizeLength(lengthRef.current);
            setLength(Math.min(100-quantized, quantizedLength));
        } 
        setDraggingStart(undefined);
        setDraggingEnd(undefined);
    }, [draggingStart, draggingEnd, start, setLength, length, setStart, setDraggingEnd, setDraggingStart, buffer]);

    useEffect(() => {
        window.addEventListener("mousemove", onMouseMove);
        window.addEventListener("touchmove", onMouseMove);
        window.addEventListener("mouseup", onMouseUp);
        window.addEventListener("touchend", onMouseUp);
        return () => {
            window.removeEventListener("mousemove", onMouseMove);
            window.removeEventListener("touchmove", onMouseMove);
            window.removeEventListener("touchend", onMouseUp);
            window.removeEventListener("mouseup", onMouseUp);
        };
    }, [draggingStart, width, draggingEnd, start, length, setLength, setStart,
        setDraggingStart, setDraggingEnd, draggingStart, buffer, bpm, startStep, silence
       ]);


    return {dragEndControl, dragStartControl, start, length};
};
