import Web3 from 'web3';
import {ethers} from 'ethers';
import {creator} from './abi/zpore-drop-creator.js';


const PINATA_BASE = "https://zequencer.mypinata.cloud/ipfs/";
const DROP_CONTRACT = "0xD301Ca66246A217f3D26da8Ebb257D3d6870603E";
const {deployed, abi} = creator;

export const newZpore = async (signer, remixURI, coverURI, caption, zorbId=1) => {
    remixURI = PINATA_BASE + remixURI;
    coverURI = PINATA_BASE + coverURI;
    const contract = new ethers.Contract(deployed.goerli, abi, signer);
    const x = await contract.purchase(
        DROP_CONTRACT,
        [
            remixURI,
            coverURI,
            caption,
            zorbId
        ],
        {
            value: 0
        });

    console.log('purchase returned', x);
    console.log('waiting...');
    return x;
};
