import ZorbAnimation from './ZorbAnimation.js';
import ENSName from '../ens/ENSName.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import ZporeLoading from './ZporeLoading.js';
import ZorbRemixes from './ZorbRemixes.js';
import {useFlags} from 'flags/flags.js';
import SliceRecorder from './SliceRecorder.js';
import {useZporeHide} from './useZporeHideUI.js';
import ZorbSelector from './ZorbSelector.js';
import { useAccount } from 'wagmi';
import React, { useEffect, useRef,useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './zpore.scss';
import Drop from '../drops/Drop.js';
import '../drops/thugger.scss';
import '../drops/nextgen.scss';

//const CID = "QmWEEvyNZmr3kngUjE8TNRRC5FmnXZLC1TdQk87UYe1cQL";
const AMPLIFY = "https://main.djdhx85izle40.amplifyapp.com/?gallery=true";
//const AMPLIFY = "https://zequencer.mypinata.cloud/ipfs/QmULQvsCWhHqwpjsPF4SjqVynt6bab2mC93P8hPF3N8nti/";

const Zpore = (props) => { 
    useFlags();
    let [finished, setFinished] = useState(false);
    let [isSlicing, setIsSlicing] = useState(false);
    let [entered, setEntered] = useState(false);
    let [remove, setRemove] = useState(false);
    let [zorbId, setZorbId] = useState();
    let [insertedZorb, setInsertedZorb] = useState();
    let [zorbStartPosition, setZorbStartPosition] = useState(undefined);
    const hideUI = false; //useZporeHide();
    const {data: account, isError, isLoading} = useAccount();
    const [started, setStarted] = useState(false);

    const clicks = useRef(0);

    useEffect(() => {
        window.addEventListener("click", () => clicks.current++);
    }, []);
    useEffect(() => {
        if (account && finished && clicks.current > 0) {
            setEntered(true);
        }
    }, [account, setEntered, finished]);

    useEffect(() => {
        window.addEventListener("message", onMessage);
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data.type === "jacker-status") {
            setStarted(true);
        }
    }, []);

    useEffect(() => {
        if (zorbId !== undefined) {
            setTimeout(() => {
                setInsertedZorb(zorbId);
            }, 1000);
        }
    }, [zorbId, setInsertedZorb]);

    useEffect(() => {
        if (entered) {
            setTimeout(() => {
                setRemove(true);
            }, 1000);
        }
    }, [entered, setRemove]);

    let className = "next-gen zpore";
    if (account) {
        className += " connected";
    }
    if (hideUI) {
        className += " hide-ui";
    }
    if (isSlicing) {
        className += " is-slicing";
    }

    return (
        <div onClick={() => console.log("root clicked")} className={className}>
          {zorbId && <ZorbRemixes/>}
          {started && <SliceRecorder setIsSlicing={setIsSlicing} zorbId={zorbId}/>}
           <ZorbSelector
             account={account}
             setZorbId={setZorbId}
             zorbId={zorbId}
             setZorbStartPosition={setZorbStartPosition}/>
          {entered && zorbStartPosition && <ZorbAnimation setZorbStartPosition={setZorbStartPosition} zorbStartPosition={zorbStartPosition}/>}
          {!remove && <div className={!entered ? "enter-overlay" : "enter-overlay hide"}>
                        <div
                          //onClick={() => setEntered(true)}
                          className="inner-overlay">
                          <img
                            className=""
                            src="/spore-logo-future4.png"/>
                          {!finished && <ZporeLoading setFinished={setFinished}/>}
           {!finished ? <div className="click-text">
                            <div>zorb + spore = zpores</div>
                            <div>
                              load your zorb into the spore for unique remixing powers
                            </div>
            </div> : <div className="click-text">
                       {account ? <button
                                    className="enter-btn"
                                    onClick={() => setEntered(true)}
                                  ><div>welcome back, <ENSName address={account.address}/></div></button> : <ConnectButton
              accountStatus="address" 
              showBalance={{ smallScreen: false, largeScreen: false }}
              label="Connect to Enter"
                                 />}
            </div>
           }
                        </div>
                      </div> 
           }
        <Drop
          zorbId={insertedZorb}
          hideWaveform={true}
          color="black"
          url={props.songId !== undefined ? AMPLIFY+"&songId=" + props.songId : AMPLIFY}
          collectionAddress={undefined} //'0x7269c1A1b8aBA1390770193771F92bD722c6a77B'
          isFullScreen={true}
          simpleControls={true}
          tokenId={1}
          className="thugger"
          artist="Keyon Christ"
          isSimple={true}
          songTitle={props.songTitle || "Psilocybin"}>
        </Drop>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Zpore);
