
import {newZpore} from 'zpore/zpore.js';
//import TextHelper from './TextHelper.js';
import {writeScreenshot} from './screenshot.js';
import {REMIX_COLLECTION_ADDRESS, REMIX_TOKEN_ID} from 'constants/zpore-addresses.js';
import {useWaveformSlicer} from './useWaveformSlicer.js';
import StepRuler from './StepRuler.js';
import React, { useEffect, useCallback, useRef, useState} from 'react';
import ipfs from 'decentralized/Ipfs.js';
import {cropAndStore} from 'utils/cropAndStore.js';
import {newSpore} from 'spores/newSpore.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import { useSigner } from 'wagmi';
import {isMobile} from 'react-device-detect';
import {useWaveformPlayer} from './useWaveformPlayer.js';
import PlayButton from 'components/buttons/PlayButton.js';
import {connect} from 'react-redux';
import SliceWaveformSVG from './SliceWaveformSVG.js';
import Captioner from './Captioner.js';
import './slice-waveform.scss';

const SliceWaveform = ({silence, screenshot, zorbId, waveform, buffer, onCancel, startStep}) => { 
    let ref = useRef();
    const {data: signer} = useSigner();
    let [caption, setCaption] = useState('');
    let [isWaitingForSignature, setIsWaitingForSignature] = useState();
    let [downloading, setDownloading] = useState(false);
    let [isCaptioning, setIsCaptioning] = useState(false);
    let [width, setWidth] = useState();
    let [download, setDownload] = useState();
    let [transaction, setTransaction] = useState();

    useEffect(() => {
        let _width = isMobile ? 300 : ref.current.offsetWidth;
        let width = (_width - 60)/2;
        setWidth(width);
    }, [setWidth]);

    const {start, length, dragEndControl, dragStartControl} = useWaveformSlicer({ref, width, buffer, startStep, silence, waveform});
    const {playhead, stop, play, isPlaying} = useWaveformPlayer({buffer, start, length});

    const post = useCallback(async () => {
        stop();
        setDownloading(true);
        let dataToStore = {caption};
        /**
           1. Crop and store on ipfs
           2. Store caption on ipfs
         */
        let remixURI = await cropAndStore(buffer, start, length);
        let imageURI = await writeScreenshot(screenshot);
        console.log("IMAGE URI=", imageURI, screenshot);

//        setIsWaitingForSignature(true);
        setDownload(remixURI);
        setDownloading(false);

        let tx = await newZpore(signer, remixURI, imageURI, caption, zorbId);
        let transactionHash = tx.hash;
        console.log("transaction=", transactionHash);
        setTransaction(transactionHash);
        await tx.wait(1);

        //let transactionHash = await newSpore(signer, 1, REMIX_COLLECTION_ADDRESS, REMIX_TOKEN_ID, cropped, json);
        setIsWaitingForSignature(false);
    }, [screenshot, caption, signer, start, setIsWaitingForSignature, setDownloading, length, setDownload]);

    return (
        <div className="slicer-backdrop">
          {downloading ? <div className="download-test">
                           <UnknownProgressBar/>
                         </div> : download ?
           (isWaitingForSignature ?
            <div className="waiting download-test">
              Open your connected wallet and sign the message to transmit the remix on-chain.
              <div className="test-net">
                This is on a test-network, as we develop this.
              </div>
              <UnknownProgressBar/>
            </div> :
            <div className="download-test">
         Download your remix <a target="_blank" href={"https://zequencer.mypinata.cloud/ipfs/" + download}>here</a>. We're still working on some things :)
              <br/>

                    <a href={"https://ropsten.etherscan.io/tx/" + transaction}>Etherscan </a>
                                                                                     </div>) : <div
            className="slice-waveform">
            <div className="control-btn-container">
              <PlayButton isPlaying={isPlaying} play={play}/>
            </div>
            <div
              ref={ref} 
              className="waveform-container">
              {isCaptioning ? <Captioner setCaption={setCaption} caption={caption}/> : width && <SliceWaveformSVG
                                                                                         silence={silence}
                                                                                         height={isMobile ? 90 : 100}
                                                                                         buffer={buffer}
                                                                                         startStep={startStep}
                                                                                         start={start} length={length} waveform={waveform} width={width}/>}
              {!isCaptioning && <div style={{left: 30 + (playhead/100)*(width)*2+'px'}} className="playhead"/>
              }
              {!isCaptioning && <div
                                  style={{left: start/100*(width*2)+'px', width: 60+length/100*(width*2)+"px"}}
                                  className="slicer">
                                  <div
                                    onMouseDown={dragStartControl}
                                    onTouchStart={dragStartControl}
                                    className="start-control">
                                    <div className="start-info">
                                      {Math.round(100*start/100*buffer[0].length/44100)/100} sec <span className="label">start</span>
                                    </div>
                                    <div className="arrow-left"/>
                                  </div>
                                  <div
                                    onTouchStart={dragEndControl}
                                    onMouseDown={dragEndControl}
                                    className="end-control">
                                    <div className="arrow-right"/>
                                  </div>
                                </div>}
            </div>
            <div className="slice-guide-info">
              Slice your remix to mint your favorite moment, for free (just gas).
            </div>
           </div>}
          {downloading ? '' : download ? <div className="slice-toolbar">
                        <div onClick={onCancel} className="done-btn pushable continue">
                          Continue
                        </div>
           </div> : <div className={isCaptioning ? "slice-toolbar captioning" : "slice-toolbar"}>
            <div onClick={isCaptioning ? () => setIsCaptioning(false) : onCancel} className="cancel-btn pushable">
              {isCaptioning ? "Edit" : "Cancel"}
            </div>
            <div onClick={() => {
                if (isCaptioning) {
                    post();
                } else {
                    setIsCaptioning(true);
                }
            }} className="done-btn pushable">
              {isCaptioning ? "Mint" : "Next"}
            </div>
                    </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceWaveform);
